@import url(https://fonts.googleapis.com/css2?family=Fira+Code&display=swap);
/* variables */
:root {
	--heading-color: rgb(0, 238, 255);
	--text-color: #262626;
	--primary-color: #000000;
	--highlight-color: #55ff00;
	--bg-color: #262626;
}

/* base styles */
body {
	font-family: Fira Code;
	margin: 0;
	font-size: 1.1em;
	background: #262626;
	background: var(--bg-color);
}
ul,
li,
p,
h1,
h2,
h3,
h4 {
	margin: 0;
	padding: 0;
}
ul {
	list-style-type: none;
}

/* layout */
.page-title {
	font-size: 1em;
	color: rgb(0, 238, 255);
	color: var(--heading-color);
	display: inline-block;
}
.btn {
	background: #fff;
	padding: 8px 12px;
	border-radius: 4px;
	color: #000000;
	color: var(--primary-color);
	cursor: pointer;
	font-size: 1em;
	border: 1px solid #000000;
	border: 1px solid var(--primary-color);
}
.btn:hover {
	color: #fff;
	background-color: #000000;
	background-color: var(--primary-color);
}

/* forms */
label {
	display: block;
	margin: 24px auto;
}
label span {
	display: block;
	margin-bottom: 6px;
}
input,
textarea {
	padding: 8px 6px;
	font-size: 1em;
	color: #777;
	width: 100%;
	box-sizing: border-box;
	border: 1px solid #ddd;
	border-radius: 4px;
}
textarea {
	min-height: 160px;
}
.error {
	color: red;
	background: pink;
	border: 1px solid red;
	border-radius: 4px;
	padding: 8px;
	margin: 10px 0;
}

.navbar {
	width: 100%;
	padding: 30px 0;
	box-sizing: border-box;
	margin-bottom: 80px;
}
.navbar ul {
	display: flex;
	margin: 0 auto;
	align-items: center;
	justify-content: flex-end;
}
.navbar .logo {
	font-weight: bold;
	color: var(--heading-color);
	letter-spacing: 1px;
	display: flex;
	align-items: center;
	margin-right: auto;
	margin-left: 20px;
}
.navbar .logo img {
	margin-right: 10px;
	width: 300px;
	margin-top: 10px;
}
.navbar a {
	color: rgb(0, 238, 255);
	text-decoration: none;
	margin-right: 20px;
}

.searchbar form {
	display: flex;
	align-items: center;
}
.searchbar input {
	margin-left: 10px;
	margin-right: 20px;
}

.sidebar {
	width: 300px;
	min-width: 300px;
	background: var(--primary-color);
	min-height: 100vh;
	box-sizing: border-box;
	position: relative;
	color: #fff;
}
.sidebar-content {
	position: fixed;
	width: inherit;
}
.sidebar .user {
	font-weight: bold;
	text-align: center;
	letter-spacing: 1px;
	padding: 40px 30px;
	border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.sidebar .links {
	margin-top: 80px;
	margin-left: 20px;
}
.sidebar .links li {
	margin-top: 10px;
}
.sidebar .links a {
	display: flex;
	padding: 10px;
	text-decoration: none;
	width: 100%;
	color: #fff;
	box-sizing: border-box;
}
.sidebar .links img {
	margin-right: 10px;
	-webkit-filter: invert(100%);
	        filter: invert(100%);
}
.sidebar .links a.active {
	color: #555;
	background: var(--bg-color);
	border-radius: 20px 0 0 20px;
}
.sidebar .links .active img {
	-webkit-filter: invert(40%);
	        filter: invert(40%);
}

.sidebar .user .avatar {
	margin-bottom: 10px;
	width: 70px;
	height: 70px;
	border: 3px solid #fff;
}

.create-form {
	max-width: 600px;
	color: #00fbff;
}

.select {
	color: #262626;
}

.new-student {
	background: #fff;
	padding: 8px 12px;
	border-radius: 4px;
	color: var(--primary-color);
	cursor: pointer;
	font-size: 1em;
	border: 1px solid var(--primary-color);
	margin-left: 10px;
	margin-top: 10px;
}
.new-student:hover {
	color: #fff;
	background-color: var(--primary-color);
}

.assignment-list {
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  grid-gap: 20px;
}
.assignment-list a {
  background-color: #fff;
  padding: 16px;
  border-radius: 6px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.05);
  text-decoration: none;
  color: inherit;
}

.assignment-list .contact-card {
  background-color: #000;
  padding: 16px;
  border-radius: 6px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.05);
  text-decoration: none;
  color: inherit;
}
.assignment-list .contact-card p {
  color: white;
}
.assignment-list h4 {
  font-size: 0.9em;
  color: var(--heading-color);
}
.assignment-list p {
  color: var(--text-color);
  font-size: 0.9em;
}
.assignment-list .assigned-to {
  margin-top: 20px;
  padding-top: 10px;
  border-top: 1px solid #eee;
}
.assignment-list ul {
  margin: 10px 0;
  display: flex;
}
.assignment-list li {
  margin-right: 10px;
}
.assignment-list .avatar {
  width: 30px;
  height: 30px;
}

.question-count em {
  margin-top: 5px;
}

#question-count {
  margin-top: 5px;
  font-size: 0.9em;
  color: white;
  text-align: center;
  /* box-shadow: 0 0 5px rgba(0, 0, 0, 0.5); */
}
