#assignment-description {
  margin-top: 20px;
  padding-top: 10px;
  border-top: 1px solid #eee;
} */

.no-assignments {
  margin-top: 50px;
}

.assignment-summary h2,
h4 {
  width: 98%;
  text-align: center;
  background-color: #262626;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  margin-bottom: 25px;
  padding: 5px;
}

.assignment-details {
  display: grid;
  grid-template-columns: 1fr;
  align-items: start;
  grid-gap: 60px;
}

.assignment-details h1 {
  margin-bottom: 25px;
}

/* assignment summary */
.assignment-summary {
  background-color: #fff;
  padding: 30px;
  border-radius: 4px;
  width: 800px;
  margin-bottom: 25px;
}
.assignment-summary .desc pre {
  margin: 10px 0;
  font-size: 0.9em;
  color: #262626;
  width: 750px;
  white-space: pre-wrap;
  overflow-wrap: break-word;
}

.assignment-summary .details {
  margin: 30px 0;
  color: var(--text-color);
  line-height: 1.8em;
  font-size: 0.9em;
}
.assignment-summary h4 {
  color: var(--text-color);
  font-size: 0.9em;
}
.assignment-summary .assigned-users {
  display: flex;
  margin-top: 20px;
}
.assignment-summary .assigned-users .avatar {
  margin-right: 10px;
}
.assignment-summary + .btn {
  margin-top: 20px;
}

.desc {
  color: black;
}

.desc code {
  display: table;
  background: rgb(232, 232, 232);
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.05);
  padding: 5px;
  margin: auto;
}

.question-btn {
  display: table;
  text-align: center;
  margin: 25px auto;
  background-color: rgb(0, 238, 255);
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgb(0, 238, 255);
  padding: 10px;
  color: black;
  text-decoration: none;
}

