h3 {
  text-align: center;
}

.contact-card h1,
.contact-card h3,
.contact-card p {
  text-align: left;
}

.github-issue {
  display: table;
  text-align: center;
  margin: 25px auto;
  background-color: rgb(0, 238, 255);
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgb(0, 238, 255);
  padding: 10px;
  color: black;
  text-decoration: none;
}


.App {
	display: flex;
	color: rgb(0, 238, 255);
}
.App .container {
	flex-grow: 1;
	padding: 0 60px;
}

