@import url('https://fonts.googleapis.com/css2?family=Fira+Code&display=swap');
/* variables */
:root {
	--heading-color: rgb(0, 238, 255);
	--text-color: #262626;
	--primary-color: #000000;
	--highlight-color: #55ff00;
	--bg-color: #262626;
}

/* base styles */
body {
	font-family: Fira Code;
	margin: 0;
	font-size: 1.1em;
	background: var(--bg-color);
}
ul,
li,
p,
h1,
h2,
h3,
h4 {
	margin: 0;
	padding: 0;
}
ul {
	list-style-type: none;
}

/* layout */
.page-title {
	font-size: 1em;
	color: var(--heading-color);
	display: inline-block;
}
.btn {
	background: #fff;
	padding: 8px 12px;
	border-radius: 4px;
	color: var(--primary-color);
	cursor: pointer;
	font-size: 1em;
	border: 1px solid var(--primary-color);
}
.btn:hover {
	color: #fff;
	background-color: var(--primary-color);
}

/* forms */
label {
	display: block;
	margin: 24px auto;
}
label span {
	display: block;
	margin-bottom: 6px;
}
input,
textarea {
	padding: 8px 6px;
	font-size: 1em;
	color: #777;
	width: 100%;
	box-sizing: border-box;
	border: 1px solid #ddd;
	border-radius: 4px;
}
textarea {
	min-height: 160px;
}
.error {
	color: red;
	background: pink;
	border: 1px solid red;
	border-radius: 4px;
	padding: 8px;
	margin: 10px 0;
}
